.hidden--tab {
    @include breakpoint(tabletOnly) {
        display: none;
    }
    @include breakpoint(desktopOnly) {
        display: inline-block;
    }
    
}

.hidden--desk,
.hidden--desk.teaser {
    @include breakpoint(desktopOnly) {
        display: none;
    }
}

.hidden--until-td {
    display: none;
    @include breakpoint(tabletAndDesktop) {
        display: inline-block;
    }

}

.hidden--mob {
    @include breakpoint(phoneOnly) {
        display: none;
    }
}

.page-overlay {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    background: rgba(0, 0, 0, 0.89);
}